import axios from "axios";
import logger from "./logService";
import {
  toast
} from "react-toastify";
import {
  getJwt
} from "./authServices";

axios.defaults.headers.common["tw-auth-token"] = getJwt()

axios.interceptors.response.use(null, error => {
  const expectedError =
    error.response &&
    error.response.status >= 400 &&
    error.response.status <= 500;
  if (!expectedError) {
    logger.log(error);
    toast.error("An unexpected error occured");
  }

  return Promise.reject(error);
});



export default {
  get: axios.get,
  post: axios.post,
  patch: axios.patch,
  delete: axios.delete
};